import * as axios from "axios";
import React from "react";
import { NavLink } from "react-router-dom";
import { usersApi } from "../../../api/api";
import avatarDefault from "../../../assets/images/avatarDefault.svg";
import s from "../users.module.css";

class User extends React.Component {
  render() {
    return (
      <div key={this.props.id}>
        <span>
          <NavLink to={"/profile/" + this.props.id}>
            <img
              src={
                this.props.photos.small != null
                  ? this.props.photos.small
                  : avatarDefault
              }
              className={s.avatar}
            />
          </NavLink>
          <div>
            {this.props.followed ? (
              <button
                disabled={this.props.followingProgres.some(
                  (id) => id === this.props.id
                )}
                onClick={() => {
                  /* debugger; */
                  this.props.unfollow(this.props.id);
                }}
              >
                unfollow
              </button>
            ) : (
              <button
                disabled={this.props.followingProgres.some(
                  (id) => id === this.props.id
                )}
                onClick={() => {
                  this.props.follow(this.props.id);
                }}
              >
                follow
              </button>
            )}
          </div>
        </span>
        <span>
          <div>{this.props.fullName}</div>
          <div>{this.props.status}</div>
        </span>
        <span>
          <div>{"props.location.country"}</div>
          <div>{"props.location.city"}</div>
        </span>
      </div>
    );
  }
}

export default User;
