import { usersApi } from "../api/api";

const FOLLOW = "FOLLOW";
const UNFOLLOW = "UNFOLLOW";
const SET_USERS = "SET-USERS";
const SET_CURRENT_PAGE = "SET-CURRENT-PAGE";
const SET_USERS_COUNT = "SET-USERS-COUNT";
const IS_FETCHING = "IS-FETCHING";
const FOLLOWING_PROGRES = "FOLLOWING-PROGRES";

export const followSuccess = (UserId) => ({
  type: FOLLOW,
  userId: UserId,
});

export const unfollowSuccess = (UserId) => ({
  type: UNFOLLOW,
  userId: UserId,
});
export const setUsers = (Users) => ({
  type: SET_USERS,
  users: Users,
});
export const setCurrentPage = (pageNumber) => ({
  type: SET_CURRENT_PAGE,
  pageNumber,
});
export const setUsersCount = (totalUsersCount) => ({
  type: SET_USERS_COUNT,
  totalUsersCount,
});
export const toggleIsFetching = (isFetching) => ({
  type: IS_FETCHING,
  isFetching,
});

export const toggleFollowingProgres = (isFetching, userId) => ({
  type: FOLLOWING_PROGRES,
  isFetching,
  userId,
});

let initialState = {
  users: [
    /* {
      id: 1,
      followed: false,
      photoUrl:
        "http://psand.ru/wp-content/uploads/2016/04/Kak-ustanovit-svoy-unikalnyiy-avatar-dlya-vseh-saytov-na-WordPress.png",
      fullName: "Andrey",
      status: "i learning React.js",
      location: { city: "Chaplynks", country: "Ukraine" },
    },
    {
      id: 2,
      followed: true,
      photoUrl:
        "http://psand.ru/wp-content/uploads/2016/04/Kak-ustanovit-svoy-unikalnyiy-avatar-dlya-vseh-saytov-na-WordPress.png",
      fullName: "Lexys",
      status: "i learning React.js",
      location: { city: "kyiv", country: "Ukraine" },
    },
    {
      id: 3,
      followed: false,
      photoUrl:
        "http://psand.ru/wp-content/uploads/2016/04/Kak-ustanovit-svoy-unikalnyiy-avatar-dlya-vseh-saytov-na-WordPress.png",
      fullName: "Maximus",
      status: "i learning React.js",
      location: { city: "Kherson", country: "Ukraine" },
    }, */
  ],
  pageSize: 5,
  totalUsersCount: 0,
  currentPage: 1,
  isFetching: false,
  followingProgres: [],
};
const UsersReducer = (state = initialState, action) => {
  switch (action.type) {
    case FOLLOW: {
      return {
        ...state,
        users: state.users.map((user) => {
          if (user.id === action.userId) {
            return { ...user, followed: true };
          }
          return user;
        }),
      };
    }

    case UNFOLLOW: {
      return {
        ...state,
        users: state.users.map((user) => {
          if (user.id === action.userId) {
            return { ...user, followed: false };
          }
          return user;
        }),
      };
    }
    case SET_USERS:
      return {
        ...state,
        users: action.users,
      };
    case SET_CURRENT_PAGE:
      return {
        ...state,
        currentPage: action.pageNumber,
      };
    case SET_USERS_COUNT:
      return {
        ...state,
        totalUsersCount: action.totalUsersCount,
      };
    case IS_FETCHING:
      return {
        ...state,
        isFetching: action.isFetching,
      };
    case FOLLOWING_PROGRES:
      return {
        ...state,
        followingProgres: action.isFetching
          ? [...state.followingProgres, action.userId]
          : state.followingProgres.filter((id) => id != action.userId),
      };
    default:
      return state;
  }
};

export const requestUsers = (currentPage, pageSize) => async (dispatch) => {
  dispatch(toggleIsFetching(true));
  const data = await usersApi.getUsers(currentPage, pageSize);
  dispatch(toggleIsFetching(false));
  dispatch(setUsers(data.items));
  dispatch(setUsersCount(data.totalCount));
};

export const followUnfollowFlow = async (
  dispatch,
  userId,
  apiMethod,
  actionCreator
) => {
  dispatch(toggleFollowingProgres(true, userId));
  const data = await apiMethod(userId);
  if (data.resultCode === 0) {
    dispatch(actionCreator(userId));
  }
  dispatch(toggleFollowingProgres(false, userId));
};
export const follow = (userId) => {
  return async (dispatch) => {
    followUnfollowFlow(dispatch, userId, usersApi.follow, followSuccess);
    /* dispatch(toggleFollowingProgres(true, userId));
    let data = await usersApi.follow(userId);
    if (data.resultCode === 0) {
      dispatch(followSuccess(userId));
    }
    dispatch(toggleFollowingProgres(false, userId)); */
  };
};

export const unfollow = (userId) => {
  return async (dispatch) => {
    followUnfollowFlow(dispatch, userId, usersApi.unfollow, unfollowSuccess);
    /* dispatch(toggleFollowingProgres(true, userId));
    usersApi.unfollow(userId).then((data) => {
      if (data.resultCode === 0) {
        dispatch(unfollowSuccess(userId));
      }
      dispatch(toggleFollowingProgres(false, userId));
    }); */
  };
};

export default UsersReducer;
