import { stopSubmit } from "redux-form";
import { profileApi, usersApi } from "../api/api";

const ADD_POST = "ADD-POST";
const UPDATE_NEW_POST = "UPDATE-NEW-POST";
const SET_USER_PROFILE = "SET-USER-PROFILE";
const GET_STATUS = "GET-STATUS";
const UPDATE_SATUS = "UPDATE-SATUS";
const UPLOAD_PHOTO_SUCCESE = "UPLOAD-PHOTO-SUCCESE";

export const addPostActionCreator = (text) => {
  return {
    type: ADD_POST,
    message: text,
  };
};

export const onPostChangeActionCreator = (text) => {
  return {
    type: UPDATE_NEW_POST,
    newText: text,
  };
};

export const setUserProfile = (profile) => ({
  type: SET_USER_PROFILE,
  profile,
});

export const getStatusAC = (status) => ({
  type: GET_STATUS,
  status,
});

export const updateStatusAC = (status) => ({
  type: UPDATE_SATUS,
  status,
});
export const uploadPhotoSuccese = (photos) => ({
  type: UPLOAD_PHOTO_SUCCESE,
  photos,
});

let initialState = {
  postsData: [
    { id: 1, message: "My first props", likeCount: 20 },
    { id: 1, message: "My first post", likeCount: 15 },
  ],
  newPostText: "It kamasutra",
  profile: null,
  status: null,
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST: {
      return {
        ...state,
        postsData: [
          ...state.postsData,
          {
            id: 5,
            message: action.message,
            likeCount: 0,
          },
        ],
        newPostText: [...state.newPostText],
        newPostText: "",
      };
    }
    case UPDATE_NEW_POST: {
      return {
        ...state,
        postsData: [...state.postsData],
        newPostText: [...state.newPostText],
        newPostText: action.newText,
      };
    }
    case SET_USER_PROFILE: {
      return {
        ...state,
        profile: action.profile,
      };
    }
    case GET_STATUS: {
      return {
        ...state,
        status: action.status,
      };
    }
    case UPDATE_SATUS: {
      return {
        ...state,
        status: action.status,
      };
    }
    case UPLOAD_PHOTO_SUCCESE: {
      return {
        ...state,
        profile: [...state.profile],
        photos: action.photos,
      };
    }
    default:
      return state;
  }
};

export const getStatus = (userId) => async (dispatch) => {
  const data = await profileApi.getStatus(userId);
  dispatch(getStatusAC(data));
};

export const updateStatus = (status) => async (dispatch) => {
  const data = await profileApi.updateStatus(status);
  if (data.resultCode === 0) {
    dispatch(updateStatusAC(status));
  }
};

export const getUserProfile = (userId) => async (dispatch) => {
  const data = await profileApi.getUserProfile(userId);
  dispatch(setUserProfile(data));
};

export const uploadPhotoProfile = (photoFile) => async (dispatch) => {
  const data = await profileApi.uploadPhoto(photoFile);
  if (data.resaultCode === 0) {
    dispatch(uploadPhotoProfile(data.data.photos));
  }
};

export const saveProfile = (profile, userId) => async (dispatch) => {
  const data = await profileApi.saveProfile(profile);

  if (data.resultCode === 0) {
    dispatch(getUserProfile(userId));
  } else {
    dispatch(stopSubmit("profileInfoData", { _error: data.messages[0] }));
    return Promise.reject(data.messages[0]);
  }
};

export default profileReducer;
