import React from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router";
import { Field, reduxForm } from "redux-form";
import { getCaptchaUrl, login } from "../../redux/AuthReducer";
import { Input } from "../utils/formControls";
import { maxLength, required } from "../utils/validator/validatorForm";

const maxLength15 = maxLength(40);
const LoginForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <div>
        <Field
          placeholder={"login"}
          component={Input}
          name={"email"}
          type={"Email"}
          validate={[required, maxLength15]}
        />
      </div>
      <div>
        <Field
          placeholder={"password"}
          component={Input}
          name={"password"}
          type={"password"}
          validate={[required]}
        />
      </div>
      <div>
        <Field component={Input} name={"rememberMe"} type={"Checkbox"} />
        Remember me
      </div>
      {props.captchaUrl && (
        <div>
          <img src={props.captchaUrl} />
          <div>
            <Field
              placeholder={"Symbols"}
              component={Input}
              name={"captchaUrl"}
              validate={[required]}
            />
          </div>
        </div>
      )}
      {props.error && <div>{props.error}</div>}
      <button>Submit</button>
    </form>
  );
};

const ReduxFormLogin = reduxForm({ form: "login" })(LoginForm);

const Login = (props) => {
  let onSubmit = (formData) => {
    console.log(formData);
    props.login(
      formData.email,
      formData.password,
      formData.rememberMe,
      formData.captchaUrl
    );
  };
  if (props.isAuth) {
    return <Redirect to={"/profile"} />;
  }
  return (
    <div>
      <h1>Login</h1>
      <ReduxFormLogin onSubmit={onSubmit} captchaUrl={props.captchaUrl} />
    </div>
  );
};

let mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth,
  captchaUrl: state.auth.captchaUrl,
});

export default connect(mapStateToProps, { login })(Login);
